@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap);
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: skyblue;
}

::-webkit-scrollbar{
  width: 10px;
}

.MobileError{
  width: 100vw;
  height: 100vh;
  font-size: 3em;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
}

a{
  color:lightskyblue;
  text-decoration: none
}

/* 
Black: #2E282A
White: #FFFFFF
Red: #F56960
Yellow: #FFD23F
Green: #2FBF71
*/

#Root{
    height:100vh;
    width:100vw;
    overflow: hidden;
    position: absolute;
}
.WindowFrame{
    background-color: transparent;
    position:absolute;

    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */

    display: grid;

    grid-template-rows: 8px 25px 100fr 8px;
    grid-template-columns: 8px 100fr 8px;

    grid-template-areas: 
        "NW N NE"
        "W TopBar E"
        "W Body E"
        "SW S SE";
}

[class^="resize"]{
    background-color: transparent;
}

.resizeN{grid-area: N; cursor: ns-resize}
.resizeE{grid-area: E; cursor: ew-resize}
.resizeS{grid-area: S; cursor: ns-resize}
.resizeW{grid-area: W; cursor: ew-resize}

.resizeNE{grid-area: NE; cursor:nesw-resize}
.resizeSE{grid-area: SE; cursor:nwse-resize}
.resizeSW{grid-area: SW; cursor:nesw-resize}
.resizeNW{grid-area: NW; cursor:nwse-resize}

.WindowTopBar{
    background-color: #2E282A;
    grid-area: TopBar;
    color: #ffffff;
    border-radius: 5px 5px 0 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.WindowTopBar:hover{
    cursor: -webkit-grab;
    cursor: grab;
}

.WindowTopBarButtons{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    min-width: 70px;
}

.WindowTopBarButton{
    border-radius: 7px;
    color: #2E282A;
    width: 14px;
    height: 14px;
    text-align: center;
    line-height: 15px;

    -webkit-transition: background-color .3s;

    transition: background-color .3s;
}

.WindowTopBarButton:hover{
    cursor: pointer;
}

#maximize{
    background-color: #2FBF71;
}

#maximize:hover{
    background-color: #0F9F51;
}

#minimize{
    background-color: #FFD23F;
}

#minimize:hover{
    background-color: #dFb21F
}

#close{
    background-color: #F56960;
}

#close:hover{
    background-color: #d54940;
}

.WindowBody{
    background-color: #ffffff;
    grid-area: Body;
    cursor: default;
    overflow-y: scroll;
}
.TerminalRoot{
    width:100%;
    background-color: #2E282A;
    height: 100%;
    color: #ffffff;
    padding: 10px;
    word-wrap: break-word;
}

.TerminalRoot input{
    position:absolute;
    opacity: 0;
    width: 100%;
}

.TerminalRoot p{
    font-family: 'Source Code Pro', monospace;
    font-size: 1em;
}

.TerminalRoot textarea{
    width:100%;
    height:100%;
    resize: none;
    color: #ffffff;
    background-color: #2e282a;
    border: none;
    outline: none;
    font-family: 'Source Code Pro', monospace;
    font-size: 1em;
}

.TerminalRoot span textarea{
    height: auto;
}

.TerminalRoot span textarea.prompt{
    width: 20px;
}
.Launcher{
    position: absolute;
    width: 75px;
    height: 30vh;
    left: -5;
    top: 35vh;
    background-color: transparent;
    border-radius: 0 10px 10px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    overflow-x: show;
}

[id^='btn-']{
    width: 80px;
    height: 60px;
    /* border: 1px solid #ffffff; */
    background-color: #2e282a;
    color: #ffffff;
    border-radius: 0 10px 10px 0;
    -webkit-transition: width .3s, height .3s;
    transition: width .3s, height .3s;
    display:flex;
    flex-direction: row-reverse;
    justify-content: space-around;
    align-items: center;
    font-size: 1em;
}

[id^='btn-'] p{
    position: absolute;
    opacity: 0;
    -webkit-transition: opacity .3s;
    transition: opacity .3s;
}

[id^='btn-']:hover{
    width: 140px;
    cursor: pointer;
}

[id^='btn-']:hover p{
    opacity: 100;
}

[id^='btn-']:active{
    height: 65px;
}

[id^='btn-'] img{
    width: 50px;
    opacity: 100;
    -webkit-transition: .3s opacity;
    transition: .3s opacity;
}

[id^='btn-']:hover img{
    opacity: 0;
}
